import React, {useMemo} from "react";
import DataTable from 'react-data-table-component';
import FilterComponent from "./FilterComponent";
import {customStyles} from "../../../utils/Common";

const TableAgents = (props) => {
  const columns = [
    {
      name: "",
      button: true,
      cell: row => <>
        <button onClick={() => props.click(row.UserId)} className="btn btn-link">
          <i className="fa fa-eye"></i>
        </button>
      </>,
      width: '50px'
    },
    {
      name: "Name",
      selector: row => row.FirstName + " " + row.LastName,
      sortable: true,
      width: '180px'
    },
    {
      name: "Company Id",
      selector: row => row.CompanyId,
      sortable: true,
      width: '140px'
    },
    {
      name: "Agency Name",
      selector: row => row.CompanyName,
      sortable: true,
      width: '280px'
    },
    {
      name: "Status",
      selector: row => row.IsActive ? <span className="text-success">Active</span> :
        <span className="text-danger">InActive</span>,
      sortable: true,
      // width: '100px'
    },
    {
      name: "Group Type",
      selector: row => row.GroupType,
      sortable: true,
      width: '130px'
    },
    {
      name: "Mobile",
      selector: row => row.PhoneNumber,
      sortable: true,
      width: '120px'
    },
    {
      name: "Total Balance",
      selector: row => row.TotalBalance,
      sortable: true,
      width: '140px'
    },
    {
      name: "Credit Limit",
      selector: row => row.TotalCreditLimit,
      sortable: true,
      width: '140px'
    },
    {
      name: "Due Amount",
      selector: row => row.DueAmount,
      sortable: true,
      width: '140px'
    },
  ];

  const [filterText, setFilterText] = React.useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(false);

  const filteredItems = props.data.filter(
    item => JSON.stringify(item).toLowerCase().indexOf(filterText.toLowerCase()) !== -1
  );

  const subHeaderComponent = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <>
        <FilterComponent onFilter={e => setFilterText(e.target.value)} onClear={handleClear} filterText={filterText}/>
      </>
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <DataTable
        columns={columns}
        data={filteredItems}
        striped
        pagination
        subHeader
        subHeaderComponent={subHeaderComponent}
        customStyles={customStyles}
      />
    </>
  )
}
export default TableAgents;
