import {CCard, CCardHeader, CCardBody, CPlaceholder} from "@coreui/react";
import React, {useEffect, useState} from "react";
import TableAgents from "../../agents/TableAgents";
import UsersTable from "./UsersTable";
import {errorToast, getCookieValue, ipAddress, tokenName} from "../../../../utils/Common";
import axios from "axios";
import {useNavigate} from "react-router-dom";

const ViewUsers = () => {
  const Placeholders = Array.from({length: 8}).map((_, index) => (
    <CPlaceholder as="p" animation="glow" key={index}>
      <CPlaceholder xs={12} size="lg"/>
    </CPlaceholder>
  ));

  const [users, setUsers] = useState(null)

  async function FetchUsers() {
    try {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/users"

      let res = await axios.get(url, {
        headers: {'Authorization': `Bearer ${token}`}
      })
      // console.log(res)
      // console.log(res.data)

      if (res.status === 200 && res.data.error) {
        errorToast(res.data.message)
      } else if (res.status === 200 && !res.data.error) {
        // console.log(res.data.recordset)
        setUsers(res.data.recordset)
      }
    } catch (e) {
      errorToast(e.message)
    }
  }

  useEffect(() => {
    FetchUsers().then()
  }, []);

  /* ---------------------------------------- */
  const navigate = useNavigate();

  const clickHandler = (UserId) => {
    // console.log(UserId)
    navigate("/agent-details", {state: UserId})
  };

  return (
    <>
      <CCard>
        <CCardHeader className="card-header-bg">
          <h4 className="mb-0 primary-color">Users Detail</h4>
        </CCardHeader>

        <CCardBody>
          {users ? users.length > 0 && <UsersTable data={users} click={clickHandler}/> : <>{Placeholders}</>}
        </CCardBody>
      </CCard>
    </>
  )
}
export default ViewUsers;
